/*    <NULL>    */
* {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: Montserrat, sans-serif;
}

*, *:before, *:after {
  box-sizing: border-box;
}

*:focus, *:active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: none;
  -webkit-overflow-scrolling: touch;
}

html {
  overflow-x: hidden;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

/*    </NULL>    */
/*    <GLOBAL>    */
:root {
  --white: rgba(255,255,255,1);
  --black: rgba(51,51,51,1);
  --red: rgba(238,85,75,1);
  --green: rgba(32,183,57,1);
  --purple: rgba(108,57,183,1);
}

*, a {
  color: var(--black);
}

.fix-cont {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  height: 100%;
  width: 100%;
}

@media (min-width: 768px) {
  .fix-cont {
    max-width: 708px;
    padding-left: 0;
    padding-right: 0;
  }
}

@media (min-width: 1200px) {
  .fix-cont {
    max-width: 1170px;
    min-width: 320px;
  }
}
